//            Кнопка навигации
// ---------------------------------

.main-nav__toggle {
  @media (max-width: 767px) {
    position: absolute;
    right: 15px;
    top: 24px;
    z-index: 2;
    width: 24px;
    height: 12px;
    cursor: pointer;
    border: none;
    background-color: inherit;
  }
}
